}

:root {
  --primary-color: #a2e4a4;
  --secondary-color: #1877f2;
  --white-color: #fff;
  --black-color: #333;
  --black-blue-color: #203856;
  --black-green-color: rgb(0, 116, 52);
}
::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgb(223, 223, 223);
  opacity: 1; /* Firefox */
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgb(223, 223, 223);
  opacity: 1; /* Firefox */
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: rgb(243, 243, 243);
}

html {
  font-family: "Be Vietnam Pro", "Quicksand", sans-serif;
  font-size: 72.5%;
  /* font-weight: 600; */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

/* Predict 1 */

.form {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 100px;
}

.form__form-group {
  width: 600px;
  margin-top: 20px;
  opacity: 0.9;
  display: flex;
  flex-direction: column;
}

.form__title {
  margin: 10px;
  font-size: 35px;
  font-weight: 700;
  color: rgb(245, 239, 230);
  background-color: rgba(0, 0, 0, 0.8);
  padding: 20px;
}

/* Header */
.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  z-index: 1;
  background-color: var(--primary-color);
  width: 100vw;
}

.header.active {
  background-color: var(--primary-color);
  padding-bottom: 10px;
  box-shadow: 0 10px 16px rgba(0, 0, 0, 0.1);
}

.header__wrap {
  margin: 0 -20%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  font-size: 3.5rem;
  font-weight: 700;
  font-family: "UVF";
}

.header__logo-text {
  color: var(--secondary-color);
}

.header__logo-text-right {
  color: var(--white-color);
}

.header__nav-list {
  list-style: none;
  display: flex;
  align-items: center;
  font-size: 1.6rem;
}

.header__nav-item {
  /* padding: 2px 12px; */
  color: var(--black-color);
  font-weight: 500;
  /* font-family: "Bree"; */
  display: block;
  right: 0;
}

.header__nav-item:hover {
  background-color: #fff;
  border-radius: 8px;
  -webkit-animation: hoverAnimation 0.2s ease-out forwards;
          animation: hoverAnimation 0.2s ease-out forwards;
}

.header__nav-item.active {
  border-radius: 8px;
  font-weight: 600;
  color: var(--black-blue-color);
  background-color: var(--white-color);
}

.header__nav-item + .header__nav-item {
  margin-left: 16px;
}

p.header__nav-right {
  padding: 6px 20px;
  font-size: 20px;
}

.header__img {
  background-color: aqua;
}

.danger {
  color: red;
  text-align: center;
  /* font-size: 14px; */
}

@-webkit-keyframes hoverAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes hoverAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.forget-password {
  position: relative;
  color: var(--secondary-color);
  text-decoration: none;
  z-index: 1;
  padding: 0.2rem;
  font-size: 1.5rem;
  right: 0;
  transition: color 0.3s;
}

.forget-password::after {
  content: "";
  z-index: -1;
  position: absolute;
  inset: 92% 0 0 0;
  background-color: var(--secondary-color);
  transition: inset 0.2s;
  width: 145px;
}

.forget-password:hover {
  color: white;
}

.forget-password:hover::after {
  inset: 0 0 0 0;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
}

.modal__overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.modal__body {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

/* Modal login & register */
.modal__wrap-content {
  background-color: var(--white-color);
  padding: 36px;
  min-width: 420px;
  border-radius: 12px;
  -webkit-animation: fadeIn 0.3s ease-in-out forwards;
          animation: fadeIn 0.3s ease-in-out forwards;
  backdrop-filter: blur(13px) saturate(180%);
  -webkit-backdrop-filter: blur(13px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
}

.tabs {
  display: flex;
  position: relative;
}

.tab-item {
  flex: 1 1;
  cursor: pointer;
  font-size: 2.2rem;
  font-weight: 500;
  padding: 8px 16px;
  background-color: #edfaed;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  text-align: center;
}

.tab-item.active {
  color: var(--black-blue-color);
  background-color: #b9eeb9;
}

.tabs .line {
  position: absolute;
  bottom: 0px;
  left: 0;
  height: 4px;
  width: 100%;
  border-radius: 24px;
  transition: all 0.2s linear;
  background-color: var(--primary-color);
}

.pane-item {
  margin: 16px 0 0;
  display: none;
}

.pane-item.active {
  display: block;
}

.form-group {
  display: flex;
  flex-direction: column !important;
  margin-bottom: 12px;
}

.form-label {
  padding: 6px 0;
  font-size: 1.6rem;
  font-weight: 800;
}

.form-control {
  height: 40px;
  font-size: 1.6rem;
  padding: 0 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  transition: all 0.2s linear;
}

.form-control::-webkit-input-placeholder {
  font-size: 1.4rem;
}

.form-control::placeholder {
  font-size: 1.4rem;
}

.wrap-btn-login {
  text-align: center;
  margin-top: 24px;
}

.btn-login {
  display: inline-block;
  color: var(--black-green-color);
  background-color: var(--primary-color);
  font-size: 1.6rem;
  font-weight: 500;
  padding: 15px 30px;
  border-radius: 10px;
  cursor: pointer;
  border: none;
}
.btn-login:hover {
  -webkit-animation: btnLoginHover 0.2s ease-in-out forwards;
          animation: btnLoginHover 0.2s ease-in-out forwards;
  opacity: 0.8;
}

@-webkit-keyframes btnLoginHover {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.8;
  }
}

@keyframes btnLoginHover {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.8;
  }
}

