.contain-container {
  display: flex;
  animation: fadeIn 0.3s ease-in-out forwards;
}
.contain {
  transform: scale(1);
}

.contain:hover {
  transform: scale(1.05);
  animation: scaleAnimation 0.4s ease-in-out;
}

@keyframes scaleAnimation {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.05);
  }
}

.contain + .contain {
  margin-left: 50px;
  /* position: relative; */
}

.contain__box {
  position: relative;
  margin: 0 auto;
  width: 500px;
  height: 830px;
  background-color: rgb(255, 255, 255);
  border-radius: 14px;
  box-shadow:
  2.8px 2.8px 2.9px rgba(0, 0, 0, 0.02),
  6.7px 6.7px 6.9px rgba(0, 0, 0, 0.028),
  12.5px 12.5px 13px rgba(0, 0, 0, 0.035),
  22.3px 22.3px 23.2px rgba(0, 0, 0, 0.042),
  41.8px 41.8px 43.4px rgba(0, 0, 0, 0.05),
  100px 100px 104px rgba(0, 0, 0, 0.07)
;

}

.contain__box__top {
  padding: 10px;
  background-color: rgb(255, 255, 255);
  border-radius: 14px;
}

.pic {
  background-image: url("https://cdn.pixabay.com/photo/2021/10/15/09/27/film-camera-6711512_960_720.jpg");
  background-size: cover;
}

.plant {
  background-image: url("https://media.istockphoto.com/photos/plant-tree-in-neutral-background-closeup-of-fresh-green-plant-picture-id1018703724?b=1&k=20&m=1018703724&s=170667a&w=0&h=scYjNguAtH_B99L9fpJUOlasc-7AA_TrPB7-Fk2P6A0=");
  background-size: cover;
  object-fit: cover;
}

.fert {
  background-image: url("https://wallpapercave.com/wp/wp1950241.jpg");
  background-size: cover;
  object-fit: cover;
}

.contain__box__picture {
  border-radius: 10px;
  max-width: 600px;
  height: 830px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.contain__box__title {
  z-index: 3;
  text-shadow: 3px 3px rgb(0, 116, 52);
  padding: 20px;
  color: white;
  font-weight: bold;
  font-size: 62px;
}

.filter {
  border-radius: 12px;
  transform: scale(0.96);
  opacity: 0.3;
  background-color: black;
  width: 500px;
  height: 860px;
  top: -1%;
  position: absolute;
  z-index: 1;
}

.contain__box__down__button {
  width: 85%;
  position: absolute;
  z-index: 3;
  margin: 0 35px;
}

.contain__button {
  background-color: white;
  padding: 10px;
  color: rgb(0, 116, 52);
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  border-radius: 10px;
  transform: translateY(-80px);
  box-shadow:
  2.8px 2.8px 2.9px rgba(0, 0, 0, 0.02),
  6.7px 6.7px 6.9px rgba(0, 0, 0, 0.028),
  12.5px 12.5px 13px rgba(0, 0, 0, 0.035),
  22.3px 22.3px 23.2px rgba(0, 0, 0, 0.042),
  41.8px 41.8px 43.4px rgba(0, 0, 0, 0.05),
  100px 100px 104px rgba(0, 0, 0, 0.07)
;

}

.contain__button:hover {
  cursor: pointer;
  background-color: rgb(0, 116, 52);
  color: white;
  animation: buttonHoverAnimation 0.2s;
}

@keyframes buttonHoverAnimation {
  from {
    background-color: white;
    color: rgb(0, 116, 52);
  }
  to {
    color: white;
    background-color: rgb(0, 116, 52);
  }
}
