/* /du-doan page */
.banner-guess-page {
  background: var(--primary-color)
    url("../../../assets/images/bg-guesspage-1.png") no-repeat left;
  background-size: 100% 100%;
  height: 100vh;
}

.guess__file-display {
  position: relative;
  font-size: 1.6rem;
  font-weight: 500;
  box-shadow: 2.8px 2.8px 2.9px rgba(0, 0, 0, 0.02),
    6.7px 6.7px 6.9px rgba(0, 0, 0, 0.028),
    12.5px 12.5px 13px rgba(0, 0, 0, 0.035),
    22.3px 22.3px 23.2px rgba(0, 0, 0, 0.042),
    41.8px 41.8px 43.4px rgba(0, 0, 0, 0.05),
    100px 100px 104px rgba(0, 0, 0, 0.07);

  background-color: var(--white-color);
  border-radius: 999px;
  margin-left: 100px;
}

.guess__file-display label {
  cursor: pointer;
  display: -webkit-box;
  width: 100%;
  padding: 0 32px;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  line-height: 48px;
  
}

.guess__file__btn-upload {
  position: absolute;
  top: 0;
  left: calc(80% - 32px);
  padding: 12px 24px;
  border-radius: 999px;
  box-shadow: 2.8px 2.8px 2.9px rgba(0, 0, 0, 0.02),
    6.7px 6.7px 6.9px rgba(0, 0, 0, 0.028),
    12.5px 12.5px 13px rgba(0, 0, 0, 0.035),
    22.3px 22.3px 23.2px rgba(0, 0, 0, 0.042),
    41.8px 41.8px 43.4px rgba(0, 0, 0, 0.05),
    100px 100px 104px rgba(0, 0, 0, 0.07);

  color: var(--white-color);
  background-color: var(--secondary-color);
  cursor: pointer;
  width: max-content;
  font-weight: 600;
  margin-left: 40px;
}

.wrap-show-img {
  display: flex;
  justify-content: center;
  margin: 40px 0;
  border: white;
  margin-left: 160px;
  
}

.wrap-show-percent {
  display: flex;
  justify-content: space-between;
  font-size: 1.8rem;
  background-color: whitesmoke;
  border-radius: 14px;
  box-shadow: 2.8px 2.8px 2.9px rgba(0, 0, 0, 0.02),
    6.7px 6.7px 6.9px rgba(0, 0, 0, 0.028),
    12.5px 12.5px 13px rgba(0, 0, 0, 0.035),
    22.3px 22.3px 23.2px rgba(0, 0, 0, 0.042),
    41.8px 41.8px 43.4px rgba(0, 0, 0, 0.05),
    100px 100px 104px rgba(0, 0, 0, 0.07);
}

.percent-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* flex: 1; */
  padding: 28px;
}

.percent-number {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  color: var(--white-color);
  background-color: var(--white-color);
  border-radius: 50%;
  font-weight: 500;
}

.percent-number.red {
  background-color: red;
}

.percent-number.yellow {
  background-color: #ffe600;
}

.percent-number.green {
  background-color: rgb(0, 190, 0);
}

.percent-disease-text {
  flex: 1;
  text-align: center;
  font-size: 1.6rem;
  margin-top: 18px;
  background-color: var(--white-color);
  margin-left: 100px;
  border-radius: 14px;
  padding: 10px 20px;
}

.predict__contain {
  margin: 0 auto;
  max-width: 600px;
  min-height: 240px;
  background-color: rgb(204, 236, 204);
  border-radius: 14px;
  box-shadow: 2.8px 2.8px 2.9px rgba(0, 0, 0, 0.02),
    6.7px 6.7px 6.9px rgba(0, 0, 0, 0.028),
    12.5px 12.5px 13px rgba(0, 0, 0, 0.035),
    22.3px 22.3px 23.2px rgba(0, 0, 0, 0.042),
    41.8px 41.8px 43.4px rgba(0, 0, 0, 0.05),
    100px 100px 104px rgba(0, 0, 0, 0.07);
  margin-top: 20px;
  margin-bottom: 20px;
}

.predict__box__picture {
  color: rgb(23, 36, 23);
  max-width: 600px;
  min-height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
}

p.predict__box__title {
  color: rgb(0, 0, 0);
  font-size: 20px;
  font-weight: 700;
}

.predict__box__down {
  padding: 0 40px;
  padding-top: 20px;
  padding-bottom: 20px;
  height: 220px;
}

.contain__Predict {
  width: 600px;
  margin-left: 100px;
  /* height: 1000px; */
  margin-top: 40px;
}

li.contain__box__down__method {
  list-style-type: circle;
  padding: 4px;
  font-size: 18px;
  text-align: justify;
}

.contain__name {
  font-size: 18px;
  text-align: justify;
}
.on__right {
  position: relative;
  left: 35vw;
}

.enable__scroll {
  overflow-y: scroll;
  height: 200px;
}

.footer {
  height: 70vh;
  /* transform: translateY(100px); */
}

.wrap-footer {
  display: none;
}

.banner__box__title_ps {
  font-size: 4.6rem;
  color: var(--black-blue-color);
  font-family: "UVF";
  width: 900px;
  margin-left: 100px;
}

.banner__box__content_ps {
  font-size: 2.4rem;
  margin: 16px 0 36px;
  line-height: 2;
  width: 800px;
  margin-left: 100px;
}